('v-row',{staticClass:"py-0",attrs:{"justify":"center"}},[_c('v-btn',{attrs:{"max-height":"30","color":"#a5bbff"},on:{"click":function($event){return _vm.changeColor(7)}}}),_c('v-btn',{attrs:{"max-height":"30","color":"#003fff"},on:{"click":function($event){return _vm.changeColor(4)}}}),_c('v-btn',{attrs:{"max-height":"30","color":"#d2a1ff"},on:{"click":function($event){return _vm.changeColor(5)}}}),_c('v-btn',{attrs:{"max-height":"30","color":"#8500ff"},on:{"click":function($event){return _vm.changeColor(6)}}})],1),_c('v-row',{staticClass:"px-3 pt-8"},[_c('v-card',{staticClass:"overflow-y-auto",attrs:{"max-height":"250"}},[_c('v-card-text',[_c('div',{staticClass:"mb-4"},[_c('h2',{staticClass:"pl-7"},[_vm._v("OctopUS 개인정보 처리 방침")]),_c('br'),_vm._v(" ('OctopUS'이하 'OctopUS')은(는) 「개인정보 보호법」 제30조에 따라 정보주체의 개인정보를 보호하고 이와 관련한 고충을 신속하고 원활하게 처리할 수 있도록 하기 위하여 다음과 같이 개인정보 처리방침을 수립·공개합니다. "),_c('br'),_c('br'),_vm._v(" ○ 이 개인정보처리방침은 2022년 1월 1부터 적용됩니다. "),_c('br'),_c('br'),_vm._v(" 제1조(개인정보의 처리 목적) "),_c('br'),_vm._v(" [ OctopUS ]('OctopUS'이하 'OctopUS')은(는) 다음의 목적을 위하여 개인정보를 처리합니다. 처리하고 있는 개인정보는 다음의 목적 이외의 용도로는 이용되지 않으며 이용 목적이 변경되는 경우에는 「개인정보 보호법」 제18조에 따라 별도의 동의를 받는 등 필요한 조치를 이행할 예정입니다. "),_c('br'),_vm._v(" 1. 홈페이지 회원가입 및 관리 "),_c('br'),_vm._v(" 회원 가입의사 확인, 회원제 서비스 제공에 따른 본인 식별·인증, 회원자격 유지·관리, 서비스 부정이용 방지 목적으로 개인정보를 처리합니다. "),_c('br'),_vm._v(" 2. 재화 또는 서비스 제공 "),_c('br'),_vm._v(" 서비스 제공, 콘텐츠 제공, 본인인증, 요금결제·정산을 목적으로 개인정보를 처리합니다. "),_c('br'),_c('br'),_vm._v(" 제2조(개인정보의 처리 및 보유 기간) "),_c('br'),_vm._v(" ① [ OctopUS ]은(는) 법령에 따른 개인정보 보유·이용기간 또는 정보주체로부터 개인정보를 수집 시에 동의받은 개인정보 보유·이용기간 내에서 개인정보를 처리·보유합니다. "),_c('br'),_vm._v(" ② 각각의 개인정보 처리 및 보유 기간은 다음과 같습니다. "),_c('br'),_c('br'),_vm._v(" 1.[ 홈페이지 회원가입 및 관리 ] [ 홈페이지 회원가입 및 관리 ]와 관련한 개인정보는 수집.이용에 관한 동의일로부터(1년)까지 위 이용목적을 위하여 보유.이용됩니다. "),_c('br'),_c('br'),_vm._v(" 제3조(처리하는 개인정보의 항목) "),_c('br'),_vm._v(" ① [ OctopUS ]은(는) 다음의 개인정보 항목을 처리하고 있습니다. "),_c('br'),_vm._v(" 1[ 홈페이지 회원가입 및 관리 ] "),_c('br'),_vm._v(" 필수항목 : 이메일, 비밀번호, 로그인ID, 서비스 이용 기록, 결제기록 "),_c('br'),_c('br'),_vm._v(" 제4조(개인정보의 제3자 제공에 관한 사항) "),_c('br'),_vm._v(" ① [ OctopUS ]은(는) 개인정보를 제1조(개인정보의 처리 목적)에서 명시한 범위 내에서만 처리하며, 정보주체의 동의, 법률의 특별한 규정 등 「개인정보 보호법」 제17조 및 제18조에 해당하는 경우에만 개인정보를 제3자에게 제공합니다. "),_c('br'),_c('br'),_vm._v(" 제5조(개인정보처리의 위탁에 관한 사항) "),_c('br'),_vm._v(" ① [ OctopUS ]은(는) 원활한 개인정보 업무처리를 위하여 다음과 같이 개인정보 처리업무를 위탁하고 있습니다. "),_c('br'),_c('br'),_vm._v(" 제6조(개인정보의 파기절차 및 파기방법) "),_c('br'),_vm._v(" ① [ OctopUS ] 은(는) 개인정보 보유기간의 경과, 처리목적 달성 등 개인정보가 불필요하게 되었을 때에는 지체없이 해당 개인정보를 파기합니다. "),_c('br'),_vm._v(" ② 정보주체로부터 동의받은 개인정보 보유기간이 경과하거나 처리목적이 달성되었음에도 불구하고 다른 법령에 따라 개인정보를 계속 보존하여야 하는 경우에는, 해당 개인정보를 별도의 데이터베이스(DB)로 옮기거나 보관장소를 달리하여 보존합니다. "),_c('br'),_vm._v(" ③ 개인정보 파기의 절차 및 방법은 다음과 같습니다. "),_c('br'),_vm._v(" 1. 파기절차 "),_c('br'),_vm._v(" [ OctopUS ] 은(는) 파기 사유가 발생한 개인정보를 선정하고, [ OctopUS ] 의 개인정보 보호책임자의 승인을 받아 개인정보를 파기합니다. "),_c('br'),_c('br'),_vm._v(" 제7조(정보주체와 법정대리인의 권리·의무 및 그 행사방법에 관한 사항) "),_c('br'),_vm._v(" ① 정보주체는 OctopUS에 대해 언제든지 개인정보 열람·정정·삭제·처리정지 요구 등의 권리를 행사할 수 있습니다. "),_c('br'),_vm._v(" ② 제1항에 따른 권리 행사는OctopUS에 대해 「개인정보 보호법」 시행령 제41조제1항에 따라 서면, 전자우편, 모사전송(FAX) 등을 통하여 하실 수 있으며 OctopUS은(는) 이에 대해 지체 없이 조치하겠습니다. "),_c('br'),_vm._v(" ③ 제1항에 따른 권리 행사는 정보주체의 법정대리인이나 위임을 받은 자 등 대리인을 통하여 하실 수 있습니다.이 경우 “개인정보 처리 방법에 관한 고시(제2020-7호)” 별지 제11호 서식에 따른 위임장을 제출하셔야 합니다. "),_c('br'),_vm._v(" ④ 개인정보 열람 및 처리정지 요구는 「개인정보 보호법」 제35조 제4항, 제37조 제2항에 의하여 정보주체의 권리가 제한 될 수 있습니다. "),_c('br'),_vm._v(" ⑤ 개인정보의 정정 및 삭제 요구는 다른 법령에서 그 개인정보가 수집 대상으로 명시되어 있는 경우에는 그 삭제를 요구할 수 없습니다. "),_c('br'),_vm._v(" ⑥ OctopUS은(는) 정보주체 권리에 따른 열람의 요구, 정정·삭제의 요구, 처리정지의 요구 시 열람 등 요구를 한 자가 본인이거나 정당한 대리인인지를 확인합니다. "),_c('br'),_c('br'),_vm._v(" 제8조(개인정보의 안전성 확보조치에 관한 사항) "),_c('br'),_vm._v(" [ OctopUS ]은(는) 개인정보의 안전성 확보를 위해 다음과 같은 조치를 취하고 있습니다. "),_c('br'),_vm._v(" 1. 개인정보의 암호화 "),_c('br'),_vm._v(" 이용자의 개인정보는 비밀번호는 암호화 되어 저장 및 관리되고 있어, 본인만이 알 수 있으며 중요한 데이터는 파일 및 전송 데이터를 암호화 하거나 파일 잠금 기능을 사용하는 등의 별도 보안기능을 사용하고 있습니다. "),_c('br'),_vm._v(" 2. 접속기록의 보관 및 위변조 방지 "),_c('br'),_vm._v(" 개인정보처리시스템